  left: -15%;
  height: 100%; }
  .clouds img {
    width: 200px;
    opacity: 0.2;
    position: absolute;
    top: 0;
    left: 0; }
    @media (max-width: 800px) {
      .clouds img {
        width: 120px; } }
    @media (min-width: 1200px) {
      .clouds img {
        width: 250px; } }
    .clouds img:first-of-type {
      animation: ltr 42s linear infinite; }
    .clouds img:nth-of-type(2) {
      animation: rtl 55s linear infinite;
      right: 0;
      left: unset;
      top: 140px; }
    .clouds img:nth-of-type(3) {
