@keyframes ltr {
  0% {
    opacity: 0.2;
    transform: translateX(0vw); }
  95% {
    opacity: 0.2;
    transform: translateX(120vw); }
  96% {
    opacity: 0;
    transform: translateX(120vw); }
  97% {
    opacity: 0;
    transform: translateX(-15vw); }
  98% {
    opacity: 0.2;
    transform: translateX(-15vw); }
  100% {
    opacity: 0.2;
    transform: translateX(0vw); } }

@keyframes rtl {
  0% {
    opacity: 0.2;
    transform: translateX(0vw); }
  95% {
    opacity: 0.2;
    transform: translateX(-120vw); }
  96% {
    opacity: 0;
    transform: translateX(-120vw); }
  97% {
    opacity: 0;
    transform: translateX(15vw); }
  98% {
    opacity: 0.2;
    transform: translateX(15vw); }
  100% {
    opacity: 0.2;
    transform: translateX(0vw); } }

@keyframes midout {
  0% {
    opacity: 0.2;
    transform: translateX(0vw); }
  50% {
    opacity: 0.2;
    transform: translateX(70vw); }
  51% {
    opacity: 0;
    transform: translateX(70vw); }
  52% {
    opacity: 0;
    transform: translateX(-75vw); }
  53% {
    opacity: 0.2;
    transform: translateX(-75vw); }
  100% {
    opacity: 0.2;
    transform: translateX(0vw); } }

